*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%}body{position:relative;width:100vw;height:100vh;background:#fa3350;background:linear-gradient(35deg, #fa3350 25%, #fa3b4a 50%, #f8493f 75%, #f65437 100%);font-family:Montserrat,sans-serif;box-sizing:border-box;display:flex;flex-direction:column;justify-content:center;align-items:center}.title{color:white;font-size:4.2rem}.wrapper{position:relative;width:65%;height:80%;display:flex;justify-content:space-around;align-items:center;flex-wrap:wrap;padding:2%;background-color:white;border:3px solid #fa3350;border-radius:10px}.wrapper .list-title{width:100%;padding-left:35%;text-align:center;font-size:1.6rem;font-weight:700;letter-spacing:1px}.wrapper .lists,.wrapper .todos{height:90%;border-radius:3px}.wrapper .buttons{position:relative;width:100%;padding:1rem;display:flex}.wrapper .buttons .add{position:absolute;bottom:0rem;right:1rem;z-index:2;width:4rem;height:4rem;background-color:#fa3350;color:white;font-size:3.6rem;text-align:center;border-radius:50%;border:none;transition:transform 0.125s, background-color 1s}.wrapper .buttons .add:hover{transform:rotate(90deg);background-color:#fa3b4a;cursor:pointer}.wrapper .buttons .add-sign{position:absolute;height:4.75rem;left:50%;top:50%;transform:translate(-50%, -50%)}.wrapper .buttons__list{position:relative;width:30%;text-align:right}.wrapper .buttons__todo{position:relative;width:65%;text-align:right}.wrapper .lists{width:30%;position:relative;overflow:auto;border-right:3px solid #fa3350}.wrapper .lists .list{min-height:6rem;height:20%;width:90%;max-height:7rem;padding:2rem 1rem;border-bottom:2px solid #fa3350;cursor:pointer;display:flex}.wrapper .lists .list__remove{margin-left:auto;border:none;background-color:transparent;padding:0;width:10%;font-size:1.6rem;font-weight:700;text-align:center;transition:transform 0.1s}.wrapper .lists .list__remove:hover{cursor:pointer;transform:scale(1.25)}.wrapper .lists .list:hover{background-color:rgba(0,0,0,0.0125)}.wrapper .lists .list__name{font-size:1.6rem;font-weight:700;letter-spacing:1px}.wrapper .todos{width:65%;padding:0 4rem;overflow:auto;position:relative}.wrapper .todos .todo{display:flex;flex-wrap:wrap;align-items:center;cursor:default;border-radius:3px;padding:1rem;min-height:10rem;margin-top:1.5rem;border-bottom:3px solid #fa3350}.wrapper .todos .todo:hover{background-color:rgba(0,0,0,0.015625)}.wrapper .todos .todo__name{margin-right:auto;font-size:2rem;font-weight:700;cursor:pointer}.wrapper .todos .todo__prio{width:10%;font-size:2.4rem;font-weight:700;text-align:center}.wrapper .todos .todo__prio.low{color:green}.wrapper .todos .todo__prio.medium{color:orange}.wrapper .todos .todo__prio.high{color:red}.wrapper .todos .todo__done{width:10%;display:flex;justify-content:center}.wrapper .todos .todo__done-box{display:block;height:1.75rem;width:1.75rem;background-color:transparent;border:1px solid black;border-radius:5px}.wrapper .todos .todo__done-box.done{background-color:#f8493f}.wrapper .todos .todo__done-box.done:hover{background-color:white}.wrapper .todos .todo__done-box:hover{cursor:pointer;background-color:#f8493f}.wrapper .todos .todo__remove{border:none;background-color:transparent;padding:0;width:10%;font-size:1.6rem;font-weight:700;text-align:center;transition:transform 0.1s}.wrapper .todos .todo__remove:hover{cursor:pointer;transform:scale(1.25)}.wrapper .todos .todo__due-date{width:100%;font-size:1.3rem}.wrapper .todos .todo__desc{width:100%;margin-top:1rem;font-size:1.4rem}.backdrop.on{visibility:visible;opacity:1}.backdrop{visibility:hidden;opacity:0;position:absolute;z-index:3;left:0;right:0;top:0;bottom:0;background-color:rgba(0,0,0,0.5)}.backdrop .modal{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);height:60vh;width:25vw;background-color:white;border-radius:10px}.backdrop .modal .hidden{display:none}.backdrop .modal form{margin:0 auto;width:100%;height:100%;display:flex;align-items:center;justify-content:center;flex-wrap:wrap}.backdrop .modal form div{display:flex;text-align:right;width:95%;font-size:1.8rem}.backdrop .modal form div label{display:block;width:30%;padding:0 1rem}.backdrop .modal form div input{padding:0.5rem 1rem}.backdrop .modal form div:last-child{display:flex;align-items:flex-start}.backdrop .modal form input,.backdrop .modal form select,.backdrop .modal form textarea{border:1px solid lightgrey;border-radius:5px}.backdrop .modal form textarea{padding:0.5rem}.backdrop .modal form input[type="submit"]{width:21%;border:none;padding:1.5rem 1rem;background-color:#fa3b4a;color:white;font-size:2rem;text-transform:uppercase;font-weight:700;border-radius:10px}.backdrop .modal form input[type="submit"]:hover{cursor:pointer}
